import React from 'react';
import Home from '../pages/Home';
import About from '../pages/About';
import Authorization from '../pages/Authorization';
import Registration from '../pages/Registration';
import PrivateRoute from './PrivateRoute';
import Hashtag from '../pages/Hashtag';
import Mentions from '../pages/Mentions';
import Page404 from '../pages/Page404';
import {Route, Switch} from 'react-router-dom';
import { Menu, Dropdown } from 'antd';
import { DownOutlined } from '@ant-design/icons';

const Navigation = (props) => {
    const {isAuthorized} = props;

    return (
        <Switch>
            <Route path="/" exact component={Home}/>
            <Route path="/about" component={About}/>
            <Route path="/login" render={() => <Authorization/>}/>
            <Route path="/register" render={() => <Registration/>}/>
            <Route path="/logout" render={() => <Authorization/>}/>
            <Route path="/hashtag/:name" render={(props) => <Hashtag {...props} />}/>
            <Route path="/mention/:name" render={(props) => <Mentions {...props} />}/>
            <Route path='/Page404' component={Page404}/>
            <Route from='*' exact={true} component={Page404}/>
        </Switch>
    );
};


export default (Navigation);