import React from 'react';
import {Form, Input, Button} from 'antd';
import {UserOutlined, LockOutlined} from '@ant-design/icons';
import {connect} from 'react-redux';
import {registerUser} from '../../store/auth/authActions';
import {Link} from 'react-router-dom';

const RegistrationForm = (props) => {

    const {getFieldDecorator} = props.form;

    const onFormSubmit = (e) => {
        e.preventDefault();
        props.form.validateFieldsAndScroll((err, {userEmail: email, lastName: name, userPassword: password}) => {
            if (!err) {
                props.registerUser({email, name, password});
            }
        });
    };

    return (
        <Form
            onSubmit={onFormSubmit}>
            <Form.Item>
                {getFieldDecorator('userEmail', {
                    rules: [{required: true, message: 'Please input your UserEmail!'}],
                })(
                    <Input
                        prefix={<UserOutlined/>}
                        type="text"
                        placeholder="Email"
                    />,
                )}
            </Form.Item>
            <Form.Item>
                {getFieldDecorator('lastName', {
                    rules: [{required: true, message: 'Please input your Name!'}],
                })(
                    <Input
                        prefix={<LockOutlined/>}
                        type="text"
                        placeholder="Name"
                    />,
                )}
            </Form.Item>
            <Form.Item>
                {getFieldDecorator('userPassword', {
                    rules: [{required: true, message: 'Please input your Password!'}],
                })(
                    <Input
                        prefix={<LockOutlined/>}
                        type="password"
                        placeholder="Password"
                    />,
                )}
            </Form.Item>
            <Form.Item style={{textAlign: 'center'}}>
                <Button type="primary" htmlType="submit">Sign up</Button>
                <p className="d-inline ml-4">
                    <Link to="/login">Log in</Link>
                </p>
            </Form.Item>
        </Form>
    );
};

export default connect(null, {registerUser})(RegistrationForm);